import {
  Box,
  Card,
  Grid,
  TextField,
  Typography,
  InputAdornment,
  Button,
} from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import SearchIcon from "@mui/icons-material/Search";
import { Link } from "react-router-dom";

interface Comment {
  id: string;
  user: {
    id: string;
    name: string;
    email: string;
    password: string;
  };
  content: string;
  postId: string;
}

interface Post {
  id: string;
  createdAt: string;
  user: {
    id: string;
    name: string;
    email: string;
    password: string;
  };
  desc: string;
  date: string;
  comments: Comment[];
}

const Feed: React.FC = () => {
  const [posts, setPosts] = useState<Post[]>([]);
  const [query, setQuery] = useState("");
  const [searchResults, setSearchResults] = useState<Post[]>([]);
  //const [post, setPost] = useState<any[]>([]); // Specify type annotation as 'any[]' or a more specific type if possible
  const displayPosts = query.length === 0 ? posts : searchResults;
  const [commentInput, setCommentInput] = useState<{
    [postId: string]: string;
  }>({});

  useEffect(() => {
    const searchPosts = async () => {
      try {
        const response = await axios.get<Post[]>(
          `http://localhost:8080/posts/${query}`
        );
        setSearchResults(response.data);
      } catch (error) {
        console.error("Error fetching search results:", error);
      }
    };
    // Fetch all posts from the backend
    const fetchPosts = async () => {
      try {
        const response = await axios.get<Post[]>(
          "http://localhost:8080/posts/access"
        );
        setPosts(response.data);
      } catch (error) {
        console.error("Error fetching posts:", error);
      }
    };

    if (query.length === 0) fetchPosts();
    if (query.length > 2) searchPosts();
  }, [query]);
  console.log(posts);

  const fetchComments = async (postId: string) => {
    try {
      const response = await axios.get<Comment[]>(`http://localhost:8080/posts/${postId}/comments`);
      
      const updatedPosts = [...posts];
      const postIndex = updatedPosts.findIndex((p) => p.id === postId);
      if (postIndex !== -1) {
        const updatedPost = { ...updatedPosts[postIndex], comments: response.data };
        updatedPosts[postIndex] = updatedPost;
        setPosts(updatedPosts);
      }
    } catch (error) {
      console.error("Error fetching comments:", error);
    }
  };

    const addComment = async (postId: string, commentContent: string) => {
      console.log('reached addcomment')
      try {
        console.log('Adding comment:', postId, commentContent);
        const response = await axios.post(
          `http://localhost:8080/posts/${postId}/comments`,
          { "user": {"id": postId}, "content": commentContent }
        );
        console.log('Comment added:', response.data);
        await fetchComments(postId); // Wait for comments to be fetched before updating state
        console.log('Comments fetched for post:', postId);
        setCommentInput((prevInput) => ({
          ...prevInput,
          [postId]: "" // Reset the comment input value for the specific postId
        }));
        console.log('Comment input cleared for post:', postId);
      } catch (error) {
        console.error("Error adding comment:", error);
      }
    };
    

  return (
    <Grid container spacing={2} sx={{ margin: "2%" }}>
      <Grid item xs={12} style={{ margin: "2%" }}>
        <Button sx={{ margin: "1% 2%" }} variant="outlined">
          <Link to="/">Home</Link>
        </Button>
        <Box>
          <TextField
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
            placeholder="Search..."
            sx={{ width: "80%", padding: "2% auto" }}
            fullWidth
            onChange={(e) => setQuery(e.target.value)}
          />
        </Box>
      </Grid>

      {displayPosts.map((p) => {
        return (
          <Grid key={p.id} item xs={12} md={6} lg={4}>
            <Card sx={{ padding: "3%", overflow: "hidden", width: "84%" }}>
              <Typography
                variant="h5"
                sx={{ fontSize: "2rem", fontWeight: "600" }}
              >
                {p.user.name}
              </Typography>
              <Typography
                sx={{ color: "#585858", marginTop: "2%" }}
                variant="body1"
              >
                Description: {p.desc}
              </Typography>

              <br />
              <br />
              <Typography
                sx={{ color: "#585858", marginTop: "2%" }}
                variant="body1"
              >
                Date: {p.date}
              </Typography>
              <TextField
                placeholder="Enter a comment"
                fullWidth
                value={commentInput[p.id] || ""}
                onChange={(e) => {
                  const updatedCommentInput = { ...commentInput };
                  updatedCommentInput[p.id] = e.target.value;
                  setCommentInput(updatedCommentInput);
                }}
              />
              
              <Button variant="contained" onClick={() => addComment(p.id, commentInput[p.id])}>
                Add Comment
              </Button>

              {p.comments.length === 0 ? (
                <p>No comments</p>
              ) : (
                <div>
                  <Typography sx={{ color: "#585858", marginTop: "2%" }} variant="body1">
                    Comments:
                  </Typography>
                  {p.comments.map((comment) => (
                    <Typography key={comment.postId} variant="body2">
                      {comment.content}
                    </Typography>
                  ))}
                </div>
              )}
            </Card>
          </Grid>
        );
      })}
    </Grid>
  );
};

export default Feed;

