import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { Home, Feed, Dashboard, Create } from "./Pages"

function App() {
  return (
    <BrowserRouter>
      <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/gathering" >
          <Route path="/gathering/dashboard" element={<Dashboard />}/>
          <Route path="/gathering/create" element={<Create />}/>
          </Route>
          <Route path="/gathering/feed" element={<Feed />}/>
      </Routes>
    </BrowserRouter>
  );
}

export default App;


