import React, { useState } from "react";
import { Typography, TextField, Button, Paper, Box } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
//const initial = { user: "", exp: 0, techs: [], desc:"" };

const Create = () => {
  const navigate = useNavigate();
  const [form, setForm] = useState<{
    id: string;
    createdAt: string;
    user: {
      id: string;
      name: string;
      email: string;
      password: string;
    };
    desc: string;
    date: string;
    comments: [];
  }>({
    id: uuidv4(),
    createdAt: "",
    user: {
      id: "",
      name: "",
      email: "",
      password: "",
    },
    desc: "",
    date: "",
    comments: [],
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    fetch("http://localhost:8080/posts/create", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(form),
    })
      .then((response) => console.log(response))
      .then((data) => {
        console.log("Success:", data);
      })
      .catch((error) => {
        console.error("Error:", error);
      });
    navigate("/gathering/feed");
  };

  const { user, desc, date } = form;
  return (
    <Paper sx={{ padding: "2%" }} elevation={3}>
      <Typography sx={{ margin: "3% auto" }} align="center" variant="h5">
        Create New Post
      </Typography>
      <form autoComplete="off" noValidate onSubmit={handleSubmit}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            flexDirection: "column",
          }}
        >
          <TextField
            type="string"
            sx={{ width: "50%", margin: "2% auto" }}
            required
            onChange={(e) =>
              setForm({ ...form, user: { ...form.user, name: e.target.value } })
            }
            label="Username"
            variant="outlined"
            value={user.name}
          />

          <TextField
            type="string"
            sx={{ width: "50%", margin: "2% auto" }}
            required
            onChange={(e) => setForm({ ...form, date: e.target.value })}
            label="Date"
            variant="outlined"
            value={date}
          />

          <TextField
            type="string"
            sx={{ width: "50%", margin: "2% auto" }}
            required
            multiline
            rows={4}
            onChange={(e) => setForm({ ...form, desc: e.target.value })}
            label="Description of the Gathering"
            variant="outlined"
            value={desc}
          />

          <Button
            sx={{ width: "50%", margin: "2% auto" }}
            variant="contained"
            type="submit"
          >
            Submit
          </Button>
        </Box>
      </form>
    </Paper>
  );
};

export default Create;

